.header__about, .header__gal { width: 300px; background: #61B82E; padding: 56px 56px 42px 46px; display: inline-block; }
@media only screen and (max-width: 767px) { .header__about, .header__gal { padding: 30px 30px 20px 30px; width: 100%; } }
.header__about h2, .header__about h3, .header__gal h2, .header__gal h3 { color: white; text-transform: uppercase; padding-bottom: 25px; border-bottom: 1px solid white; font-size: 36px; margin-bottom: 25px; font-weight: normal; }
@media only screen and (max-width: 767px) { .header__about h2, .header__about h3, .header__gal h2, .header__gal h3 { font-size: 30px; padding-bottom: 15px; margin-bottom: 15px; } }
.header__about a, .header__gal a { display: inline-block; }
.header__about p:last-of-type, .header__gal p:last-of-type { margin-bottom: 40px; }
.header__gal { width: 370px; background: #2e272f; }
.header__gal a { background: #61B82E; }
.header__logo { position: absolute; right: 0; top: 0; display: inline-block; animation: pulse 2s infinite forwards linear; }
@media only screen and (max-width: 767px) { .header__logo { position: static; margin: 0 auto; display: block; text-align: center; } }
@media only screen and (max-width: 767px) { .header__logo img { width: 150px; height: 164px; } }
.header__logo h1 { opacity: 0; font-size: 1px; color: transparent; }
.header__nav.is-visible { display: block; }
.header__burger { width: 40px; height: 40px; position: absolute; right: 10px; top: 20px; display: none; }
@media only screen and (max-width: 767px) { .header__burger { display: block; } }
.header__burger span { display: block; height: 4px; background: white; margin-top: 7px; }

.content p { color: #727176; font-size: 16px; margin-bottom: 15px; }
@media only screen and (max-width: 1024px) { .content p { font-size: 15px; } }
@media only screen and (max-width: 767px) { .content p { font-size: 14px; } }
.content p:last-of-type { margin-bottom: 0; }
