.content .block_has-aside .inner { *zoom: 1; }
.content .block_has-aside .inner:after { content: " "; display: table; clear: both; }
.content .block__content { width: 850px; float: left; *zoom: 1; }
.content .block__content:after { content: " "; display: table; clear: both; }
@media only screen and (max-width: 1200px) { .content .block__content { width: 100%; float: none; } }
.content .block__aside { width: 300px; float: right; height: 100%; padding: 0 0 0 15px; }
