.content ul { margin-bottom: 25px; }
.content li { font-size: 16px; padding-bottom: 10px; border-bottom: 1px solid rgba(114, 113, 118, 0.2); margin-bottom: 10px; color: #727176; position: relative; padding-left: 25px; }
.content li:last-of-type { border-bottom: none; }
.content li:before { display: block; content: ""; position: absolute; left: 0; top: 5px; background: url("../img/arr-right.svg"); width: 15px; height: 15px; background-size: cover; }

.links { display: flex; justify-content: space-around; align-items: stretch; flex-wrap: wrap; }
