.links__curtains { background-image: url("../img/icons.png"); width: 133px; height: 133px; background-position: 0px 0px; }
.links__paper { background-image: url("../img/icons.png"); width: 133px; height: 133px; background-position: -137px 0px; }

.intro { background: #dad9da; }
.intro .inner { padding: 20px 10px; *zoom: 1; }
.intro .inner:after { content: " "; display: table; clear: both; }
.intro__img { background: url("../img/hat.jpg"); width: 267px; height: 411px; float: left; margin-right: 30px; }
@media only screen and (max-width: 767px) { .intro__img { float: none; margin: 0 auto; width: 150px; height: 231px; background-size: cover; display: block; } }
.intro__text-wrap { margin-top: 30px; }
@media only screen and (max-width: 767px) { .intro__text-wrap { margin-top: 10px; text-align: center; } }
.intro__text-wrap h3 { font-size: 30px; color: #727176; }
@media only screen and (max-width: 1024px) { .intro__text-wrap h3 { font-size: 25px; } }
@media only screen and (max-width: 767px) { .intro__text-wrap h3 { font-size: 22px; margin-bottom: 0; } }
.intro__text-wrap h3 span { font-size: 55px; color: #61B82E; }
@media only screen and (max-width: 1024px) { .intro__text-wrap h3 span { font-size: 50px; } }
@media only screen and (max-width: 767px) { .intro__text-wrap h3 span { font-size: 40px; } }
.intro__text-wrap strong { color: black; font-size: 36px; display: block; margin-bottom: 30px; }
@media only screen and (max-width: 1024px) { .intro__text-wrap strong { font-size: 33px; } }
@media only screen and (max-width: 767px) { .intro__text-wrap strong { font-size: 30px; margin-bottom: 10px; } }

.footer { background: #2e272f; }
.footer .inner { padding: 50px 10px 80px 10px; display: flex; justify-content: space-between; align-items: stretch; flex-wrap: wrap; }
@media only screen and (max-width: 1024px) { .footer .inner { justify-content: center; } }
@media only screen and (max-width: 767px) { .footer .inner { display: block; text-align: center; padding-top: 30px; } }
.footer p { color: #727176; font-size: 15px; text-transform: uppercase; font-weight: bold; margin-bottom: 5px; padding-left: 70px; position: relative; }
@media only screen and (max-width: 767px) { .footer p { font-size: 14px; padding-left: 0; } }
@media only screen and (max-width: 767px) { .footer p:first-of-type { padding-top: 50px; } }
.footer p:first-of-type:before { display: block; content: ""; width: 40px; height: 40px; background: url("../img/home.svg"); background-size: cover; position: absolute; left: 0; top: 0; }
@media only screen and (max-width: 767px) { .footer p:first-of-type:before { top: 5px; left: 50%; transform: translateX(-50%); } }
@media only screen and (max-width: 1024px) { .footer__address { margin: 0 20px; } }
@media only screen and (max-width: 767px) { .footer__address { display: inline-block; margin: 0; } }
.footer__mail { border-right: 1px solid #727176; border-left: 1px solid #727176; padding: 0 50px; }
@media only screen and (max-width: 1200px) { .footer__mail { padding: 0 30px; } }
@media only screen and (max-width: 1024px) { .footer__mail { margin: 0 20px; border: none; } }
@media only screen and (max-width: 767px) { .footer__mail { padding: 20px 0; margin: 30px 0; border-top: 1px solid #727176; border-bottom: 1px solid #727176; } }
.footer__mail a { color: #727176; font-size: 15px; text-transform: uppercase; display: inline-block; vertical-align: middle; font-weight: bold; }
@media only screen and (max-width: 767px) { .footer__mail a { padding-top: 50px; position: relative; } }
.footer__mail a:before { display: inline-block; content: ""; width: 37px; height: 37px; background: url("../img/mail.svg"); background-size: cover; margin-right: 10px; vertical-align: middle; }
@media only screen and (max-width: 767px) { .footer__mail a:before { position: absolute; top: 5px; left: 50%; transform: translateX(-50%); } }
@media only screen and (max-width: 1024px) { .footer__copy { margin-top: 30px; } }
.footer__copy p:first-of-type:before { background: url("../img/mask.svg"); width: 50px; height: 50px; background-size: cover; }
@media only screen and (max-width: 767px) { .footer__copy p:first-of-type:before { top: -5px; } }
.footer__to-top { position: absolute; width: 60px; height: 55px; background: #61B82E; left: 50%; bottom: 0; margin-left: -30px; padding-top: 15px; }
@media only screen and (max-width: 767px) { .footer__to-top { width: 55px; height: 50px; } }
.footer__to-top span { display: block; background-image: url("../img/icons.png"); width: 31px; height: 19px; background-position: -219px -184px; margin: 0 auto; }

.intro-line { padding: 21px 0; background: #61B82E; }
.intro-line h1 { color: white; font-weight: bold; font-size: 40px; text-transform: uppercase; }
@media only screen and (max-width: 1024px) { .intro-line h1 { font-size: 38px; } }
@media only screen and (max-width: 767px) { .intro-line h1 { font-size: 35px; } }

.who img { margin-right: 20px; float: left; margin-bottom: 20px; }
@media only screen and (max-width: 767px) { .who img { float: none; margin: 0 auto 20px auto; display: block; } }
.who img:nth-last-of-type(odd) { float: right; margin-left: 20px; margin-right: 0; }
@media only screen and (max-width: 767px) { .who img:nth-last-of-type(odd) { float: none; margin: 0 auto 20px auto; } }

.why { display: flex; justify-content: space-between; align-items: stretch; flex-wrap: wrap; }
@media only screen and (max-width: 1200px) { .why { justify-content: space-around; } }
.why__item { padding: 25px 40px; background: #2e272f; margin-bottom: 30px; width: 350px; }
@media only screen and (max-width: 767px) { .why__item { width: 100%; padding: 20px; } }
.why__item span { color: white; font-size: 21px; position: relative; padding-left: 60px; display: inline-block; margin-bottom: 25px; }
.why__item span i { position: absolute; width: 45px; height: 45px; background-size: cover; left: 0; top: 0; display: inline-block; }
.why__item-actors { background: url("../img/actor.svg"); }
.why__item-costume { background: url("../img/costume.svg"); }
.why__item-show { background: url("../img/shows.svg"); }
.why__item-money { background: url("../img/money-save.svg"); }
.why__item-satisfy { background: url("../img/satisfy.svg"); }

.photo, .video { display: flex; justify-content: space-between; align-items: flex-start; flex-wrap: wrap; margin-top: 20px; }
.photo:after, .video:after { display: none !important; }
.photo iframe, .video iframe { margin-bottom: 20px; width: 49%; height: 400px; }
@media only screen and (max-width: 1024px) { .photo iframe, .video iframe { width: 600px; margin: 0 auto 20px auto; } }
@media only screen and (max-width: 767px) { .photo iframe, .video iframe { width: 100%; height: 350px; } }
@media only screen and (max-width: 400px) { .photo iframe, .video iframe { height: 300px; } }

.photo__img { width: 24%; height: auto; display: block; margin-bottom: 20px; cursor: pointer; }
@media only screen and (max-width: 1024px) { .photo__img { width: 32%; } }
@media only screen and (max-width: 767px) { .photo__img { width: 49%; } }
@media only screen and (max-width: 400px) { .photo__img { width: 100%; margin-bottom: 10px; } }
.photo__overlay { position: fixed; width: 100%; left: 0; top: 0; right: 0; bottom: 0; background: rgba(0, 0, 0, 0.8); z-index: 10; display: none; }
.photo__overlay.is-visible { display: block; }
.photo__slider-wrap { position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); border: 2px solid white; }
.photo__slider-wrap img { display: block; margin: 0 auto; width: auto; max-width: 900px; height: auto; }
.photo__slider-close { position: absolute; top: -40px; right: -40px; background: url("../img/cancel.svg"); width: 30px; height: 30px; background-size: cover; }
.photo__slider-left, .photo__slider-right { width: 40px; height: 40px; background: url("../img/slider-arr.svg"); position: absolute; top: 50%; right: -50px; margin-top: -20px; background-size: cover; }
.photo__slider-left { transform: scale(-1); right: 0; left: -50px; }

.rep .block__content { float: right; }
.rep .block__aside { float: left; padding: 0 15px 0 0; }
.rep .block__aside ul { margin-left: 20px; display: none; }
.rep .block__aside ul.is-visible { display: block; }
.rep .block__aside li { cursor: pointer; }
.rep .block__aside li:hover { color: white; font-weight: bold; }
.rep .block__aside li.is-active { color: #61B82E; font-weight: bold; }
.rep h2 { cursor: pointer; font-size: 18px; }
.rep h2 span { width: 15px; height: 15px; background: url("../img/arr-down.svg"); display: inline-block; background-size: cover; margin-left: 5px; vertical-align: middle; transition: none; }
