.services__item img { float: left; margin: 0 20px 20px 0; }
.services__item:nth-of-type(even) img { float: right; margin: 0 0 20px 20px; }

.actual b { text-decoration: underline; font-size: 16px; margin-bottom: 15px; display: block; color: white; }

.congrats { *zoom: 1; }
.congrats:after { content: " "; display: table; clear: both; }
.congrats iframe { float: left; margin: 0 20px 20px 0; }

#map { width: 100%; height: 400px; margin-bottom: 30px; }

.contacts { margin-bottom: 80px; }
.contacts__box { display: flex; justify-content: space-between; align-items: stretch; flex-wrap: wrap; }
.contacts__info { width: 300px; }
.contacts form { font-size: 0; width: 67%; }
.contacts form input[type="text"], .contacts form input[type="number"], .contacts form textarea { width: 50%; display: inline-block; padding: 5px 15px; margin-bottom: 20px; transition: none; font-size: 16px; background: #dad9da; border: 2px solid black; }
